<template>
  <div id="about">
    <div class="wrapper">
      <div class="cards-wrapper">
        <card v-bind:header="groupIntroduction.header"
            v-bind:text="groupIntroduction.text"
            grid="grid-2"
            v-bind:button="groupIntroduction.button.text"
            v-bind:link="groupIntroduction.button.link">
        </card>
      </div>
    </div>
  </div>
</template>

<style>
#home .wrapper {
  max-width:1000px;
  margin:40px auto;
  padding: 0 16px;
}

.cards-wrapper {
  margin-bottom: 20px;
  display: inline-block;
  width: 100%

}
</style>

<script>
import Card from './reuseable/Card'

export default {
  data () {
    if (window.localStorage.language === 'chi') {
      return {
        groupIntroduction: {
          header: '集团介绍',
          text: '浙江春晖集团有限公司浙江省批企业集团，国家大型企业，全国500家最大规模机械工业企业之一，国家高新技术企业，是浙江省纳税大户、上虞市最具影响力的大企业大集团......'
        }
      }
    } else {
      return {
        groupIntroduction: {
          header: 'Introduction',
          text: 'Zhejiang Chunhui Group co. is a national large enterprise, one of the top 500 machining enterprises, and a high-tech enterprise of the provincial "five as one batch". Now it owns six holding companies and three shares companies (one of their will be listed )...'
        }
      }
    }
  },
  components: {
    Card
  }
}
</script>



