<style>

.card {
  background: #fff;
  box-shadow: 0px 1px 3px #aaa;
  margin: 16px;
}

.card .context-wrapper{
  padding: 16px;
}

.card .button-wrapper {
  text-align: right;
  padding-right: 16px;
  padding-bottom: 16px;
}

.card .button-wrapper a {
  cursor: pointer;
  color: #1C5F30;
  padding: 8px 16px;
  border-radius: 5px;
  transition: 0.2s;
}

.card .button-wrapper a:hover {
  background-color: #E0E0E0;
}

.card p {
  margin:0;
  font-size: 15px;
}

.card h1 {
  padding-left: 12px;
  border-left: 4px solid #1C5F30;
  font-size: 24px;
  margin-bottom: 0;
  display: inline-block;
}

@media screen and (min-width: 800px) {
  .grid-2{
    width: 50%;
    float: left;
  }

  .grid-3{
    width: 33.333333%;
    float: left;
  }

  .grid-4{
    width: 25%;
    float: left;
  }
}
</style>

<template>
  <div v-bind:class="grid">
    <div class="card" >
      <h1>{{header}}</h1>
      <div class="context-wrapper">
        <p>{{text}}</p>
      </div>
      <div class="button-wrapper" v-if="button">
        <a v-bind:href="link">{{button}}</a>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  props: ['grid', 'header', 'text', 'button', 'link']
}
</script>



